@font-face {
    font-family: 'fixedsys';
    src: url('fsex302-webfont.woff2') format('woff2'),
         url('fsex302-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

html, body {
	height: 100%;
	width: 100%;
	box-sizing: border-box;
	margin: 0px;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	user-select: none;
}

textarea, input, [contenteditable] {
	user-select: initial;
}

body {
	display: flex;
	flex-direction: column;
}

html, body, input, button, select {
	font-family: fixedsys;
	font-size: 12pt;
}

.viewport {
	position: relative;
	align-self: stretch;
	flex: 1;
	overflow: hidden;
}

.pane {
	border: 2px solid;
	border-image: url(/border.png) 2 2 2 2;
	background-color: #c3c3c3;
	display: flex;
	flex-direction: column;
	box-sizing: border-box;
}

.frame {
	overflow: auto;
	flex-direction: column;
	flex-basis: 100%;
}

.frame .frame {
	border: 1px solid #808080;
}

.padded {
	padding: 0.5em;
}

.frame.margin {
	margin: 0.5em;
}

.liquid {
	display: flex;
	flex: 1;
}

.scroll {
	flex: 1;
	overflow: auto;
}

.resize {
	resize: both;
}

.resize-vert {
	resize: vertical;
}

.resize-hor {
	resize: horizontal;
}

.white {
	background-color: white;
}

.row {
	display: flex;
	flex-direction: row;
	align-content: center;
}

.cols {
	display: flex;
	flex-direction: row;
}

.rows {
	display: flex;
	flex-direction: column;
}

.wrap {
	flex-wrap: wrap;
}

.spacer {
	flex: 1;
}

/*label > *:first-child + * {
	margin-left: 0.5em;
}
*/
.frame.inset, .inset {
	border: 2px solid;
	border-image: url(/border-inset.png) 2 2 2 2;
	border-color: #c3c3c3;
}

.window .editor-toolbar{
	padding: 0px;
}
.window .editor-toolbar::before{
	margin: 0px;
}
.window .editor-toolbar::after{
	margin: 0px;
}

.window .editor-toolbar a,
.window .editor-toolbar i.separator {
	border-image: url(/border.png) 2 2 2 2 fill;
}

.CodeMirror {
	flex: 1;
}

button {
	font-family: fixedsys;
	font-size: 12pt;
	border-style: solid;
	border-image: url(/border.png) 2 2 2 2 fill;
	border-width: 2px 2px 2px 2px;
	border-image-width: 2px 2px 2px 2px;
	outline-offset: -4px;
	text-align: center;
}

.window button {
	min-width: 120px;
}

.window button.tight {
	min-width: initial;
}

button:active {
	outline-offset: -5px;
	border-image: url(/border-depressed.png) 2 3 3 2 fill;
	border-width: 3px 1px 1px 3px;
	border-image-width: 2px 3px 3px 2px;
}

*:focus {
	outline-width: 1px;
	outline-color: black;
	outline-style: dotted;
}

label {
	display: flex;
	align-items: center;
}

input[type="checkbox"],
input[type="radio"] {
	margin: 0.5em;
}

hr {
	margin: 0px;
}

textarea {
	resize: none;
}

progress {
	display: block;
	width: 100%;
	-webkit-appearance: none;
}

progress::-webkit-progress-bar {
	background-color: transparent;
	padding-left: 1px;
	padding-right: 1px;
}
progress::-webkit-progress-value {
	background-color: transparent;
	background-image: url(/progress.png);
	background-repeat: repeat-x;
	background-position: left center
}

ul.task-list {
	background-color: black;
	color: white;
	position: fixed;
	top:0px;
	right:1em;
	text-align: right;
	z-index: 999999999;
	opacity: 0.75;
	pointer-events: none;
	mix-blend-mode: darken;
}

ul.task-list:hover {
}

label.icon-label {
	white-space: nowrap;
	text-align: right;
}

label.icon-frame label {
	display: none;
}

.repo-browser .treeview {
	/*display: flex;
	flex: 1;*/
}

.repo-browser .treeview .content {
	/*display: flex;
	flex: 1;*/
}

.repo-browser .treeview > .resize > .folder {
	min-width: 4em;
}

.repo-browser .treeview .resize {
	white-space: nowrap;
	resize: horizontal;
	overflow: auto;
	height: 100%;
}

.repo-browser .main-content {
	padding: 1em;
}
.repo-browser pre.main-content {
	padding: 0.25em 1em;
}

.json-view:not(.top-level) {
	display: contents;
}

:not(.expanded) > .json-view-body {
	display: none;
}

.json-view.main-content {
	background-color: black;
	color: white;
}

.json-view .json-view-body {
	margin-left : 2em;
}

.image-control.main-content {
	display: flex;
	flex: 1;
	justify-content:center;
	align-items:center;
	background-color: #C3C3C3;
}

.image-control.main-content img {
	padding: 1em;
}

.plaintext-control {
	margin: 0px;
	flex: 1;
}

.html-control.main-content {
	display: flex;
	flex: 1;
	border: none;
	padding: 0px;
	margin: 1em;
}

.html-control iframe {
	flex: 1;
}


[data-type=object] > .json-key > span{
	cursor: pointer;
	text-decoration: underline;
}


.phpEditor.loading .scroll {
	background-image: url(/loading.gif);
	background-repeat: no-repeat;
	background-position: center;
}

.CodeMirror-selectedtext {
	background-color: #000082 !important;
	color: white;
}

.wide:not(td):not(th) {
	display: flex;
	flex-grow: 1;
}

.row.tight > * {
	margin: 0px;
	justify-content: center;
}

.phpEditor .ace_editor .ace_scroller {
	background-color: transparent;
}

.phpEditor .resize-vert {
	overflow-y: scroll;
}

.phpEditor .ace_editor,
.phpEditor textarea {
	margin: 0px;
	padding: 1em 0px;
	flex-grow: 1;
	height: auto !important;
}

.over-hide {
	overflow: hidden;
}

/*.phpEditor .ace_editor {
	min-height: 18em;
}
*/
.scroll-header {
	position: sticky;
	top: 0px;
}

.left-align {
	text-align: left;
	align-items: flex-start;
}

.scroll-header {
	min-height: 24px;
}

.tight > label {
	padding-left: 0.5em;
	padding-right: 0.5em;
}

.ace_line:last-child {
	background-color: #444;
	color: white;
}

.output-line {
	position:absolute;
	z-index: 10000;
	background-color: #000033;
	color: white;
	background-blend-mode: color-burn;
}

.ace_markdown-code-unclosed-string {
	color: rgba(200,120,120,1);
	font-weight: bold;
}

/*.ace_whitespace.ace_xml,*/
.ace_punctuation.ace_xml-decl.ace_xml,
.ace_markdown-open-code-tag,
.ace_markdown-generic-code-tag {
	opacity: 0.5;
	display: none;

}
